import React from "react";
import CardListSection from "../components/CardListSection";
import DesignSection from "../components/DesignSection";
import FloatingCTA from "../components/FloatingCTA";
import Footer from "../components/Footer";
import Hero from "../components/Hero";
import Page from "../components/Page";

const appPageData = [
    {
        src: "../assets/app-design/desktop/image-airfilter.jpg",
        title: "Airfilter",
        description:
            "Solving the problem of poor indoor air quality by filtering the air",
    },
    {
        src: "../assets/app-design/desktop/image-eyecam.jpg",
        title: "Eyecam",
        description:
            "Product that lets you edit your favorite photos and videos at any time",
    },
    {
        src: "../assets/app-design/desktop/image-faceit.jpg",
        title: "Faceit",
        description:
            "Get to meet your favorite internet superstar with the faceit app",
    },
    {
        src: "../assets/app-design/desktop/image-todo.jpg",
        title: "Todo",
        description: "A todo app that features cloud sync with light and dark mode"
    },
    {
        src: "../assets/app-design/desktop/image-loopstudios.jpg",
        title: "Loopstudios",
        description: "A VR experience app made for Loopstudios"
    }
];

export default function AppDesignPage(props) {
    return (
        <Page id="app">
            <Hero
                heading="App Design"
                description="Our mobile designs bring intuitive digital solutions to your customers right at their fingertips."
            />
            <CardListSection data={appPageData} />
            <DesignSection currentPage={props.currentPage} changeCurrentPage={props.changeCurrentPage}/>
        </Page>
    );
}
