import React from "react";
import LocationCard from "./LocationCard";
import styled from "styled-components";
import Fade from "react-reveal";

const locationData = [
    {
        src: "../assets/locations/tablet/image-map-canada.png",
        country: "Canada",
        office: {
            officeName: "Designo Central Office",
            address: "3886 Wellington Street",
            address2: "Toronto, Ontario M9C 3j5",
        },
        contact: {
            phone: "+1 253-863-8967",
            email: "contact@designo.co",
        },
        effect: "left",
    },
    {
        src: "../assets/locations/tablet/image-map-australia.png",
        country: "Australia",
        office: {
            officeName: "Designo AU Office",
            address: "19 Balonne Street",
            address2: "New South Wales 2443",
        },
        contact: {
            phone: "(02) 6720 9092",
            email: "contact@designo.au",
        },
        effect: "right",
    },
    {
        src: "../assets/locations/tablet/image-map-uk.png",
        country: "United Kingdom",
        office: {
            officeName: "Designo UK Office",
            address: "13  Colorado Way",
            address2: "Rhyd-y-fro SA8 9GA",
        },
        contact: {
            phone: "078 3115 1400",
            email: "contact@designo.uk",
        },
        effect: "left",
    },
];

const Section = styled.section`
`;

export default function LocationSection() {
    return (
        <Section>
            {locationData.map((location, index) => {
                return (
                    <Fade left={location.effect === "left"} right={location.effect === "right"}>
                        <LocationCard
                            src={location.src}
                            country={location.country}
                            officeName={location.office.officeName}
                            address={location.office.address}
                            address2={location.office.address2}
                            phone={location.contact.phone}
                            email={location.contact.email}
                            key={index}
                            index={index}
                            effect={location.effect}
                        />
                    </Fade>
                );
            })}
        </Section>
    );
}
