import React from "react";
import styled from "styled-components";
import { color } from "../Styles/GlobalStyle";
import Logo from "../UI/Logo";
import facebook from "../assets/shared/desktop/icon-facebook.svg";
import instagram from "../assets/shared/desktop/icon-instagram.svg";
import youtube from "../assets/shared/desktop/icon-youtube.svg";
import twitter from "../assets/shared/desktop/icon-twitter.svg";
import pinterest from "../assets/shared/desktop/icon-pinterest.svg";

const StyledFooter = styled.footer`
    background-color: ${color.black};
    padding: ${(props) =>
            props.currentPage === "contact" ? "4rem" : "15.8125rem"}
        0 4rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: ${(props) =>
        props.currentPage === "home" || props.currentPage === "contact"
            ? "200px"
            : "300px"};

    @media all and (min-width: 768px) {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        text-align: left;
        row-gap: 2rem;
        padding-inline: 5.4%;
        padding-top: ${(props) => props.currentPage === "contact" && "5rem"};
    }

    @media all and (min-width: 768px) {
        padding-inline: 11.4%;
    }
            
    @media all and (min-width: 1800px) {
        padding-inline: 15%;
    }

    .logo {
        border-bottom: 1px solid ${color.darkGrey};
        width: 90%;
        padding-bottom: 2rem;
        grid-column: 1 / 4;
        grid-row: 1 / 2;

        @media all and (min-width: 768px) {
            width: 100%;
        }
    }
`;
const FooterLinks = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
    padding-top: 2rem;
    width: 100%;

    @media all and (min-width: 768px) {
        flex-direction: row;
        grid-column: 2 / 4;
        grid-row: 1 / 2;
        justify-content: flex-end;
        margin: 0;
        padding: 0;
    }
`;

const Link = styled.a`
    color: ${color.white};
    text-decoration: none;
    text-transform: uppercase;
    font-size: 0.875rem;
    letter-spacing: 2px;
    margin-bottom: 2rem;
    position: relative;

    @media all and (min-width: 768px) {
        margin-right: 2.625rem;

        &:last-of-type {
            margin-right: 0;
        }
    }

    @media all and (min-width: 1200px) {
        &::after {
            content: "";
            background-color: #ffffff50;
            position: absolute;
            bottom: -5px;
            left: 0;
            width: 0;
            transition: all 0.5s;
        }

        &:hover::after {
            width: 100%;
            height: 1px;
        }
    }
`;

const Content = styled.div`
    color: ${color.white};
    opacity: 0.7;
    margin-bottom: 2.5rem;

    @media all and (min-width: 768px) {
        margin: 0;
    }

    h4 {
        font-size: 1rem;
        line-height: 1.625rem;
        font-weight: 500;
    }

    a {
        display: block;
        color: inherit;
        text-decoration: none;
    }
`;

const SocialMediaContainer = styled.div`
    display: flex;
    justify-content: space-between;

    @media all and (min-width: 768px) {
        align-self: end;
        justify-content: initial;
        justify-self: end;
    }

    a {
        margin-right: 1rem;

        img {
            transition: all 0.25s;

            @media all and (min-width: 1200px) {
                &:hover {
                    filter: brightness(1.56);
                }
            }
        }

        &:last-of-type {
            margin: 0;
        }
    }
`;

const socialMedia = [
    { icon: facebook, website: "https://www.facebook.com/" } ,
    { icon: youtube, website: "https://www.youtube.com/" } ,
    {icon: twitter, website: "https://www.twitter.com/"} ,
    {icon: pinterest, website: "https://www.pinterest.com/"} ,
    {icon: instagram, website: "https://www.instagram.com/"} ,
];

export default function Footer(props) {
    return (
        <StyledFooter currentPage={props.currentPage}>
            <div className="logo">
                <Logo dark />
            </div>
            <FooterLinks>
                <Link
                    href="#about"
                    onClick={() => props.changeCurrentPage("about")}
                >
                    our company
                </Link>
                <Link
                    href="#locations"
                    onClick={() => props.changeCurrentPage("locations")}
                >
                    locations
                </Link>
                <Link
                    href="#contact"
                    onClick={() => props.changeCurrentPage("contact")}
                >
                    contact
                </Link>
            </FooterLinks>
            <Content>
                <h4>Designo Central Office</h4>
                <a href="#">3886 Wellington Street</a>
                <a href="#">Toronto, Ontario M9C 3J5</a>
            </Content>
            <Content>
                <h4>Contact Us (Central Office)</h4>
                <a href="#">P : +1 253-863-8967</a>
                <a href="#">M : contact@designo.co</a>
            </Content>
            <SocialMediaContainer>
                {socialMedia.map((icon, index) => {
                    return (
                        <a
                            key={index}
                            href={icon.website}
                        >
                            <img src={icon.icon} />
                        </a>
                    );
                })}
            </SocialMediaContainer>
        </StyledFooter>
    );
}
