import React from "react";
import styled from "styled-components";
import { color } from "../Styles/GlobalStyle";

const StyledButton = styled.button`
    background: ${(props) =>
        props.background === "white" ? color.white : color.peach};
    color: ${(props) =>
        props.background === "white" ? color.darkGrey : color.white};
    padding: 1rem 1.5rem;
    border-radius: 10px;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 1px;
    transition: all 0.2s;
    cursor: pointer;
    border: none;
    outline: none;
    width: 152px;

    &:hover {
        background: ${color.lightPeach};
        color: ${color.white};
    }
`;

export default function Button(props) {
    return (
        <StyledButton type={props.type} background={props.background}>{props.children || "learn more"}</StyledButton>
    );
}
