import React from "react";
import CardListSection from "../components/CardListSection";
import DesignSection from "../components/DesignSection";
import FloatingCTA from "../components/FloatingCTA";
import Footer from "../components/Footer";
import Hero from "../components/Hero";
import Page from "../components/Page";

const webDesignData = [
    {
        src: "../assets/web-design/desktop/image-express.jpg",
        title: "Express",
        description:
            "A multi-carrier shipping website for ecommerce businesses",
    },
    {
        src: "../assets/web-design/desktop/image-transfer.jpg",
        title: "Transfer",
        description:
            "Site for low-cost money transfers and sending money within seconds",
    },
    {
        src: "../assets/web-design/desktop/image-photon.jpg",
        title: "Photon",
        description:
            "A state-of-the-art music player with high-resolution audio and DSP effects",
    },
    {
        src: "../assets/web-design/desktop/image-builder.jpg",
        title: "Builder",
        description:
            "Connects users with local contractors based on their location",
    },
    {
        src: "../assets/web-design/desktop/image-blogr.jpg",
        title: "Blogr",
        description:
            "Blogr is a platform for creating an online blog or publication",
    },
    {
        src: "../assets/web-design/desktop/image-camp.jpg",
        title: "Camp",
        description:
            "Get expert training in coding, data, design, and digital marketing",
    },
];

export default function WebDesignPage(props) {
    return (
        <Page id="web">
            <Hero
                heading="Web Design"
                description="We build websites that serve as powerful marketing tools and bring memorable brand experiences."
            />
            <CardListSection data={webDesignData}/>
            <DesignSection currentPage={props.currentPage} changeCurrentPage={props.changeCurrentPage}/>
        </Page>
    );
}
