import { useState } from "react";
import Navbar from "./UI/Navbar";
import GlobalStyle from "./Styles/GlobalStyle";
import HomePage from "./HomePage/HomePage";
import styled from "styled-components";
import bg from "./assets/shared/desktop/bg-pattern-leaf.svg";
import WebDesignPage from "./WebDesignPage/WebDesignPage";
import AppDesignPage from "./AppDesignPage/AppDesignPage";
import GraphicDesignPage from "./GraphicDesignPage/GraphicDesignPage";
import AboutPage from "./AboutPage/AboutPage";
import FloatingCTA from "./components/FloatingCTA";
import Footer from "./components/Footer";
import LocationPage from "./LocationsPage/LocationPage";
import ContactPage from "./ContactPage/ContactPage";

const StyledApp = styled.div`
    background-image: url(${bg});
    background-repeat: no-repeat;
    background-position: 0% 33%;
    position: relative;

    /* @media screen and (min-width: 768px) {
        margin-top: 163px;
    } */
`;

function App() {
    const [currentPage, setCurrentPage] = useState("home");
    const changeCurrentPage = (page) => {
        setCurrentPage(page);
    };

    const abc = 123

    console.log(Object.keys(abc))

    return (
        <StyledApp className="App">
            <GlobalStyle />
            <Navbar changeCurrentPage={changeCurrentPage} />
            {currentPage === "home" && (
                <HomePage
                    currentPage={currentPage}
                    changeCurrentPage={changeCurrentPage}
                />
            )}
            {currentPage === "web" && (
                <WebDesignPage
                    currentPage={currentPage}
                    changeCurrentPage={changeCurrentPage}
                />
            )}
            {currentPage === "app" && (
                <AppDesignPage
                    currentPage={currentPage}
                    changeCurrentPage={changeCurrentPage}
                />
            )}
            {currentPage === "graphic" && (
                <GraphicDesignPage
                    currentPage={currentPage}
                    changeCurrentPage={changeCurrentPage}
                />
            )}
            {currentPage === "about" && (
                <AboutPage
                    currentPage={currentPage}
                    changeCurrentPage={changeCurrentPage}
                />
            )}
            {currentPage === "locations" && (
                <LocationPage
                    currentPage={currentPage}
                    changeCurrentPage={changeCurrentPage}
                />
            )}
            {currentPage === "contact" && (
                <ContactPage
                    currentPage={currentPage}
                    changeCurrentPage={changeCurrentPage}
                />
            )}
            {currentPage !== "contact" && <FloatingCTA changeCurrentPage={changeCurrentPage}/>}
            <Footer changeCurrentPage={changeCurrentPage} currentPage={currentPage}/>
        </StyledApp>
    );
}

export default App;
