import React from "react";
import styled from "styled-components";
import { color } from "../Styles/GlobalStyle";
import bg from "../assets/shared/desktop/bg-pattern-two-circles.svg";

const Card = styled.div`
    display: flex;
    flex-direction: column;
    margin-bottom: 2.5rem;

    .desktop {
        display: none;
    }

    @media all and (min-width: 1200px) {
        display: grid;
        grid-template-columns: ${(props) =>
            props.index === 1 ? "2fr 1fr" : "1fr 2fr"};
        align-items: center;
        column-gap: 1.5rem;

        .mobile {
            display: none;
        }
        .desktop {
            display: block;
        }
    }
`;

const Img = styled.img`
    width: 100%;
    min-height: 320px;
    object-fit: cover;

    @media all and (min-width: 768px) {
        border-radius: 10px;
        margin-bottom: 1.9375rem;
    }

    @media all and (min-width: 768px) {
        margin-bottom: 0;
        order: ${(props) => (props.index === 1 ? "1" : "0")};
    }
`;

const Content = styled.div`
    background-color: #fdf3f0;
    background-image: url(${bg});
    background-repeat: no-repeat;
    text-align: center;
    padding: 5rem 1.5rem;

    @media all and (min-width: 768px) {
        border-radius: 10px;
        padding-inline: 4.6875rem;
        text-align: left;
        display: grid;
        grid-template-columns: 1fr 1fr;

        .contact {
            grid-column: 2 / 3;
        }

        .office {
            grid-column: 1 / 2;
        }
    }

    @media all and (min-width: 1200px) {
        height: 100%;
        padding-inline: 15%;
    }
`;
const Country = styled.h2`
    font-size: 2rem;
    font-weight: 500;
    line-height: 2.25rem;
    color: ${color.peach};
    margin-bottom: 1.5rem;

    @media all and (min-width: 1200px) {
        align-self: center;
        margin: 0;
    }
`;
const ContactContainer = styled.div`
    margin-bottom: 1.5rem;

    @media all and (min-width: 1200px) {
        margin-bottom: 0;
    }

    &:last-of-type {
        margin: 0;
    }

    h4,
    p {
        font-size: 0.9375rem;
        line-height: 1.5625rem;
    }

    h4 {
        font-weight: 700;
    }
`;

export default function LocationCard(props) {
    return (
            <Card index={props.index}>
                <Img
                    className="mobile"
                    src={require("../" + props.src.slice(3)).default}
                />
                <Img
                    index={props.index}
                    className="desktop"
                    src={
                        require("../" +
                            props.src.replace("tablet", "desktop").slice(3))
                            .default
                    }
                />
                <Content>
                    <Country>{props.country}</Country>
                    <ContactContainer className="office">
                        <h4>{props.officeName}</h4>
                        <p>{props.address}</p>
                        <p>{props.address2}</p>
                    </ContactContainer>
                    <ContactContainer className="contact">
                        <h4>Contact</h4>
                        <p>P : {props.phone}</p>
                        <p>M : {props.email}</p>
                    </ContactContainer>
                </Content>
            </Card>
    );
}
