import React from "react";
import { createGlobalStyle } from "styled-components";

const GlobalStyles = createGlobalStyle`
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        font-family: Jost, sans-serif;
    }

    html, body {
        overflow-x: hidden;
        scroll-behavior: smooth;
    }

    body {
        font-size: 1rem;
        line-height: 1.625rem;
        font-weight: 400;
    }

    h1 {
        font-size: 2rem;
        line-height: 2.25rem;
        font-weight: 500;

        @media all and (min-width: 768px) {
            font-size: 3rem;
            line-height: 3rem;
        }
    }

    h2 {
        font-size: 2.5rem;
        line-height: 3rem;
        letter-spacing: 2px
    }

    h3 {
        font-size: 1.25rem;
        letter-spacing: 5px;
    }
`;

const color = {
    peach: "#E7816B",
    black: "#1F1C1E",
    white: "#FFF",
    lightPeach: "#FFAD9B",
    darkGrey: "#333136",
    lightGrey: "#F1F3F5",
};
export default function GlobalStyle() {
    return <GlobalStyles />;
}

export { color };
