import React from "react";
import styled from "styled-components";
import { color } from "../Styles/GlobalStyle";
import Button from "../UI/Button";
import heroImg from "../assets/home/desktop/image-hero-phone.png";
import Fade from "react-reveal";

const StyledCard = styled.section`
    background-color: ${color.peach};
    background-image: url(${require("../assets/home/desktop/bg-pattern-hero-home.svg").default});
    background-repeat: no-repeat;
    background-position: 150% 50%;
    padding: 5rem 6.4% 28.1875rem;
    position: relative;
    height: 52.6875rem;
    overflow: hidden;
    margin-bottom: 7.5rem;

    @media all and (min-width: 768px) {
        padding: 3.75rem 8.4% 0;
        margin-inline: auto;
        border-radius: 15px;
    }

    @media all and (min-width: 1200px) {
        padding: 9.0625rem 17.6% 0;
        height: auto;
        display: flex;
        padding-left: 8%;
        padding-block: 9.0625rem;
        margin-bottom: 10rem;
    }

`;

const Content = styled.div`
    text-align: center;
    margin: 0 auto;

    @media all and (min-width: 768px) {
        width: 70%;
    }

    @media all and (min-width: 1200px) {
        text-align: left;
        margin-left: 0;
        width: 50%;
    }
    
`;

const Heading = styled.h1`
    color: ${color.white};
    margin-bottom: 1.9rem;

    @media all and (min-width: 768px) {
        margin-bottom: 1.5rem;
    }

    @media all and (min-width: 1200px) {
        margin-bottom: 1.75rem;
    }
`;

const Paragraph = styled.p`
    color: ${color.white};
    margin-bottom: 1.7rem;

    @media all and (min-width: 768px) {
        margin-bottom: 1.1875rem;
        line-height: 1.8rem;
    }

    @media all and (min-width: 1200px) {
        margin-bottom: 2.5rem;
        letter-spacing: 0.5px;
    }
`;

const Img = styled.img`
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 320px;

    @media all and (min-width: 1200px) {
        left: 75%;
        top: -50px;
    }
`;

export default function HomeHero(props) {
    return (
        <Fade top cascade>
            <StyledCard>
                <Content>
                    <Heading>
                        Award-winning custom designs and digital branding
                        solutions
                    </Heading>
                    <Paragraph>
                        With over 10 years in the industry, we are experienced
                        in creating fully responsive websites, apps, and
                        engaging brand experiences. Find out more about our
                        services.
                    </Paragraph>
                    <a
                        href="#about"
                        onClick={() => props.changeCurrentPage("about")}
                    >
                        <Button background="white" />
                    </a>
                </Content>
                <Img src={heroImg} />
            </StyledCard>
        </Fade>
    );
}
