import React, { useState, useEffect, useRef } from "react";
import styled, { keyframes } from "styled-components";

const shake = keyframes`
    25% {right: 5px}
    50% {right: 0}
    75% {right: 5px}
    100% {right: 0}
`

const Container = styled.div`
    position: absolute;
    right: 0;
    top: 65%;
    transform: translateY(-65%);
    display: flex;
    align-items: center;
    animation: ${shake} 0.5s;
`;

const Message = styled.span`
    font-size: 0.9rem;
    font-style: italic;
    font-weight: 400;
    margin-right: 1rem;
`;



export default function EmptyMessage(props) {
    return (
        <Container value={props.value}>
            <Message>Can't be empty</Message>
            <img
                src={
                    require("../assets/contact/desktop/icon-error.svg").default
                }
                alt="error icon"
            />
        </Container>
    );
}
