import React, { useState } from "react";
import Button from "../UI/Button";
import Input from "../UI/Input";
import styled from "styled-components";
import SuccessSubmit from "./SuccessSubmit";

const StyledForm = styled.form`
    display: grid;
    grid-template-columns: 1fr 1fr;
    place-items: center;

    div,
    button,
    textarea {
        grid-column: 1 / 3;
    }

    @media all and (min-width: 768px) {
        button {
            grid-column: 2 / 3;
            justify-self: end;
        }
    }
`;

const TextArea = styled.textarea`
    border: none;
    border-bottom: 3px solid #fff;
    border-width: ${(props) => (props.value === "" ? "1px" : "3px")};
    width: 100%;
    color: #fff;
    padding-left: 0.875rem;
    padding: 0.6875rem 0.875rem;
    margin-block: 1rem 2.5rem;
    font-weight: 500;
    font-size: 0.9375rem;
    line-height: 1.625rem;
    transition: all 0.25s;
    background-color: transparent;
    outline: none;

    &:empty {
        border-width: 1px;
        opacity: 0.5;
    }

    &::placeholder {
        color: #fff;
        font-weight: 400;
    }

    &:hover,
    &:focus {
        opacity: 1;
        border-width: 3px;
    }

    @media all and (min-width: 768px) {
        padding-left: 1.5rem;
    }

    @media all and (min-width: 1200px) {
        padding-left: 1rem;
    }
`;


export default function ContactForm() {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [phone, setPhone] = useState("");
    const [message, setMessage] = useState("");

    const handleName = (e) => {
        setName(e.target.value);
    };

    const handleEmail = (e) => {
        setEmail(e.target.value);
    };

    const handlePhone = (e) => {
        setPhone(e.target.value);
    };

    const handleMessage = (e) => {
        setMessage(e.target.value);
    };

    const resetForm = () => {
        setName("");
        setEmail("");
        setPhone("");
        setMessage("");
    };

    const [isFieldEmpty, setIsFieldEmpty] = useState(false);

    const displayErrorMessage = (bool) => {
        setIsFieldEmpty(bool);
    };


    const handleSubmit = () => {
        if (name === "" || email === "" || phone === "") {
            displayErrorMessage(true);
            return false;
        }
        displayErrorMessage(false);
        resetForm();
        setIsFormSubmitted(true)
        return true;
    };

    const [isFormSubmitted, setIsFormSubmitted] = useState(false)
    const closeSnackbar = () => {
        setIsFormSubmitted(false)
    }

    return (
        <StyledForm onSubmit={handleSubmit}>
            <Input
                type="text"
                id="name"
                placeholder="Name"
                value={name}
                onChange={handleName}
                isFieldEmpty={isFieldEmpty}
            />
            <Input
                type="email"
                id="email"
                placeholder="Email Address"
                value={email}
                onChange={handleEmail}
                isFieldEmpty={isFieldEmpty}
            />
            <Input
                type="number"
                id="phone"
                placeholder="Phone"
                value={phone}
                onChange={handlePhone}
                isFieldEmpty={isFieldEmpty}
            />
            <TextArea
                type="textarea"
                id="message"
                placeholder="Your Message"
                rows="4"
                value={message}
                onChange={handleMessage}
                isFieldEmpty={isFieldEmpty}
            ></TextArea>
            <Button background="white" type="submit">
                Submit
            </Button>
            {isFormSubmitted && <SuccessSubmit isFormSubmitted={isFormSubmitted} closeSnackbar={closeSnackbar}/>}
        </StyledForm>
    );
}
