import React from "react";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

export default function SuccessSubmit(props) {
    function Alert(props) {
        return <MuiAlert elevation={6} variant="filled" {...props} />;
    }

    return (
        <div>
            <Snackbar anchorOrigin={{ vertical: "bottom", horizontal: "left" }} open={props.isFormSubmitted} autoHideDuration={3000} onClose={props.closeSnackbar}>
                <Alert onClose={props.closeSnackbar} severity="success">Thank you for the contact</Alert>
            </Snackbar>
        </div>
    );
}
