import React from "react";
import styled from "styled-components";
import DesignCard from "./DesignCard";
import Zoom from "react-reveal";

const StyledContainer = styled.section`
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 90%;
    margin: 0 auto 7.5rem;
    row-gap: 1.5rem;
    column-gap: 1.875rem;

    .web,
    .app,
    .graphic {
        grid-column: 1 / 3;
    }

    .web {
        display: ${props => props.currentPage === "home" || props.currentPage === "app" || props.currentPage === "graphic" ? "block" : "none"}
    }

    .app {
        display: ${props => props.currentPage === "app" || props.currentPage === "app" ? "none" : "block"}
    }

    .graphic {
        display: ${props => props.currentPage === "graphic" ? "none" : "block"}
    }

    @media all and (min-width: 768px) {
        width: 100%;
    }

    @media all and (min-width: 1200px) {
        margin-bottom: 10rem;

        .web {
            grid-column: ${props => props.currentPage === "graphic" ? "2 / 3" : "1 / 2"};
            grid-row: ${props => props.currentPage === "home" ? "1 / 3" : "1 / 2"};
            height: 100%;
        }

        .app,
        .graphic {
            grid-column: ${props => props.currentPage === "home" ? "2 / 3" : "1 / 3"};
        }

        .app {
            grid-column: ${props => (props.currentPage === "web" && "1 / 2") || (props.currentPage === "graphic" && "1 / 2")};
        }

        .graphic {
            grid-column: ${(props => props.currentPage === "web" || props.currentPage === "app") && "2 / 3"};
        }
    }
        
    @media all and (min-width: 1800px) {
    }
`;

export default function DesignSection(props) {

    return (
        <Zoom right>
            <StyledContainer currentPage={props.currentPage}>
                <DesignCard currentPage={props.currentPage} className="web" design="web" onClick={() => props.changeCurrentPage("web")}/>
                <DesignCard currentPage={props.currentPage} className="app" design="app" onClick={() => props.changeCurrentPage("app")}/>
                <DesignCard currentPage={props.currentPage} className="graphic" design="graphic" onClick={() => props.changeCurrentPage("graphic")}/>
            </StyledContainer>
        </Zoom>
    );
}
