import React from "react";
import styled from "styled-components";
import { color } from "../Styles/GlobalStyle";
import Button from "../UI/Button";
import bg from "../assets/shared/desktop/bg-pattern-call-to-action.svg";

const Card = styled.div`
    width: 90%;
    margin: 0 auto;
    border-radius: 10px;
    background-color: ${color.peach};
    background-image: url(${bg});
    background-position: center;
    background-repeat: no-repeat;
    padding: 4rem 1.5rem;
    text-align: center;
    position: absolute;
    bottom: 720px;
    left: 0;
    right: 0;

    @media all and (min-width: 768px) {
        bottom: 300px;
        
    }

    @media all and (min-width: 1200px) {
        width: 78%;
        bottom: 350px;
        
    }
            
    @media all and (min-width: 1800px) {
        width: 70%;
    }
`;

const Content = styled.div`
    width: 90%;
    margin: 0 auto;
    color: ${color.white};

    @media all and (min-width: 1200px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        text-align: left;
    }

    .button {
        justify-self: end;
    }
`;

const Heading = styled.h2`
    font-size: 2rem;
    line-height: 2.25rem;
    font-weight: 500;

    @media all and (min-width: 768px) {
        font-size: 2.5rem;
        line-height: 2.5rem;
        width: 50%;
        margin: 0 auto;
    }

    @media all and (min-width: 1200px) {
        grid-column: 1 / 2;
        width: 100%;
    }
`;

const Paragraph = styled.p`
    font-size: 0.9375rem;
    line-height: 1.5625rem;
    margin-block: 0.75rem 2rem;

    @media all and (min-width: 768px) {
        width: 80%;
        margin: 1.5rem auto 2rem;
        font-size: 1rem;
        line-height: 1.8;

    }

    @media all and (min-width: 1200px) {
        grid-column: 1 / 2;
        width: 100%;
        letter-spacing: 0.5px;

    }
    
`;

export default function FloatingCTA(props) {
    return (
        <Card>
            <Content>
                <Heading>Letâ€™s talk about your project</Heading>
                <Paragraph>
                    Ready to take it to the next level? Contact us today and
                    find out how our expertise can help your business grow.
                </Paragraph>
                <a href="#contact" className="button" onClick={() => props.changeCurrentPage("contact")}>
                    <Button background="white">Get in Touch</Button>
                </a>
            </Content>
        </Card>
    );
}
