import React from "react";
import styled from "styled-components";
import { color } from "../Styles/GlobalStyle";
import heroBg from "../assets/about/desktop/bg-pattern-hero-about-desktop.svg";
import normalBg from "../assets/shared/desktop/bg-pattern-three-circles.svg";

const StyledCard = styled.section`
    display: grid;
    grid-template-rows: 320px 1fr;

    @media all and (min-width: 768px) {
        border-radius: 10px;
        margin: 0 auto 7.5rem;
    }

    @media all and (min-width: 1200px) {
        grid-template-columns: ${(props) =>
            props.hero ? "1.5fr 1fr" : "1fr 1.5fr"};
        grid-template-rows: ${props => props.hero ? "80%" : "100%"};
        margin-bottom: 0;
        max-height: 700px;
    }
`;

const Img = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;

    &.tablet,
    &.desktop {
        display: none;
    }

    @media all and (min-width: 768px) {
        border-radius: 10px 10px 0 0;

        &.mobile {
            display: none;
        }

        &.tablet {
            display: block;
        }
    }

    @media all and (min-width: 1200px) {
        border-radius: ${(props) =>
            props.hero || props.order ? "0 10px 10px 0" : "10px 0 0 10px"};
        order: ${(props) => (props.hero || props.order ? "1" : "0")};

        &.tablet {
            display: none;
        }

        &.desktop {
            display: block;
        }
    }
    
`;

const Content = styled.div`
    text-align: center;
    background-color: ${(props) => (props.hero ? color.peach : "#FDF3F0")};
    background-image: url(${(props) => (props.hero ? heroBg : normalBg)});
    background-repeat: no-repeat;
    background-size: cover;
    display: grid;
    place-items: center;

    @media all and (min-width: 768px) {
        border-radius: 0 0 10px 10px;
    }

    @media all and (min-width: 1200px) {
        justify-items: center;
        border-radius: ${(props) =>
            props.hero || props.order ? "10px 0 0 10px" : "0 10px 10px 0"};
        text-align: left;
    }
`;

const Heading = styled.h1`
    font-size: 2rem;
    line-height: 2.25rem;
    font-weight: 500;
    color: ${(props) => (props.hero ? color.white : color.peach)};
    margin-block: 3.5rem 1.5rem;

    @media all and (min-width: 768px) {
        font-size: 3rem;
        line-height: 3rem;
    }

    @media all and (min-width: 1200px) {
        width: 70%;
        align-self: end;
        margin-top: 0;
    }
`;

const Description = styled.p`
    font-size: 0.9375rem;
    line-height: 1.5625rem;
    width: 80%;
    margin-bottom: 5rem;
    color: ${(props) => (props.hero ? color.white : color.dark)};

    @media all and (min-width: 768px) {
        font-size: 1rem;
        line-height: 1.8;
    }

    @media all and (min-width: 1200px) {
        align-self: start;
        margin-top: 2.5%;
        width: 70%;
        margin-bottom: 0;
        letter-spacing: 0.5px;
    }

`;

export default function AboutCard(props) {
    return (
        <StyledCard hero={props.hero}>
            <Img
                hero={props.hero}
                order={props.order}
                className="mobile"
                src={require("../" + props.mobile.slice(3)).default}
            />
            <Img
                hero={props.hero}
                order={props.order}
                className="tablet"
                src={require("../" + props.tablet.slice(3)).default}
            />
            <Img
                hero={props.hero}
                order={props.order}
                className="desktop"
                src={require("../" + props.desktop.slice(3)).default}
            />
            <Content hero={props.hero} order={props.order}>
                <Heading hero={props.hero}>{props.heading}</Heading>
                <Description hero={props.hero}>{props.description}</Description>
            </Content>
        </StyledCard>
    );
}
