import React from "react";
import styled from "styled-components";
import mobileWeb from "../assets/home/mobile/image-web-design.jpg";
import tabletWeb from "../assets/home/tablet/image-web-design.jpg";
import desktopWeb from "../assets/home/desktop/image-web-design-large.jpg";
import mobileApp from "../assets/home/mobile/image-app-design.jpg";
import tabletApp from "../assets/home/tablet/image-app-design.jpg";
import desktopApp from "../assets/home/desktop/image-app-design.jpg";
import mobileGraphic from "../assets/home/mobile/image-graphic-design.jpg";
import tabletGraphic from "../assets/home/tablet/image-graphic-design.jpg";
import desktopGraphic from "../assets/home/desktop/image-graphic-design.jpg";
import arrow from "../assets/shared/desktop/icon-right-arrow.svg";
import { color } from "../Styles/GlobalStyle";

const Card = styled.a`
    border-radius: 10px;
    background-image: url(${(props) => (props.design === "web" && mobileWeb) || (props.design === "app" && mobileApp) || (props.design === "graphic" && mobileGraphic)});
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #00000080;
    background-blend-mode: multiply;
    text-align: center;
    padding-block: 5.625rem;
    position: relative;
    transition: background 0.3s, transform 0.5s;

    @media all and (min-width: 768px) {
        background-image: url(${(props) => (props.design === "web" && tabletWeb) || (props.design === "app" && tabletApp) || (props.design === "graphic" && tabletGraphic)});
    }

    @media all and (min-width: 1200px) {
        background-image: url(${(props) => (props.design === "web" && desktopWeb) || (props.design === "app" && desktopApp) || (props.design === "graphic" && desktopGraphic)});
        height: 308px;
        cursor: pointer;

        &:hover {
            background-color: #E7816B;
            background-blend-mode: screen;
            transform: scale(1.05);

            &::after {
                content: '';
                position: absolute;
                inset: 0;
                background: #00000030;
                mix-blend-mode: multiply;
                border-radius: inherit;
            }
        }
    }

`;

const Content = styled.div`
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
`;

const Design = styled.h2`
    text-transform: uppercase;
    color: #fff;
    font-size: 1.5rem;
    font-weight: 500;
    line-height: 30px;

    @media all and (min-width: 768px) {
        font-size: 2.5rem;
        line-height: 3rem;
    }
`;

const Projects = styled.h3`
    color: #fff;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 13px;

    &::after {
        content: url(${arrow});
        display: inline-block;
        margin-left: 0.5rem;
    }

    @media all and (min-width: 768px) {
        font-size: 0.9375rem;
        margin-top: 1.5rem;
    }
`;

export default function DesignCard(props) {
    return (
        <Card
            href="#"
            className={props.className}
            design={props.design}
            onClick={props.onClick}
        >
            <Content>
                <Design>{props.design} design</Design>
                <Projects>View Projects</Projects>
            </Content>
        </Card>
    );
}
