import React from "react";
import Icon from "../UI/Icon";
import styled from "styled-components";
import Button from "../UI/Button";

const Section = styled.section`
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin: 7.5rem auto;

    @media all and (min-width: 1200px) {
        flex-direction: row;
        margin: 10rem auto;
    }

`;

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 3rem;

    @media all and (min-width: 768px) {
        margin-bottom: 5rem;
    }

    @media all and (min-width: 1200px) {
        margin-bottom: 0;
        margin-right: 1.875rem;
    }

    &:last-of-type {
        margin-bottom: 0;
    }
`;

const Country = styled.h3`
    font-size: 1.25rem;
    line-height: 1.625rem;
    letter-spacing: 5px;
    text-transform: uppercase;
    font-weight: 500;
    margin-block: 3rem 2rem;
`;

export default function LocationSection(props) {
    return (
        <Section>
            <Container key="canada">
                <Icon icon="../assets/shared/desktop/illustration-canada.svg" />
                <Country>Canada</Country>
                <a href="#locations" onClick={() => props.changeCurrentPage("locations")}>
                    <Button>see location</Button>
                </a>
            </Container>
            <Container key="australia">
                <Icon icon="../assets/shared/desktop/illustration-australia.svg" />
                <Country>australia</Country>
                <a href="#locations" onClick={() => props.changeCurrentPage("locations")}>
                    <Button>see location</Button>
                </a>
            </Container>
            <Container key="uk">
                <Icon icon="../assets/shared/desktop/illustration-united-kingdom.svg" />
                <Country>United Kingdom</Country>
                <a href="#locations" onClick={() => props.changeCurrentPage("locations")}>
                    <Button>see location</Button>
                </a>
            </Container>
        </Section>
    );
}
