import React, { useState } from "react";
import styled from "styled-components";
import Logo from "./Logo";
import menu from "../assets/shared/mobile/icon-hamburger.svg";
import closeButton from "../assets/shared/mobile/icon-close.svg";
import { color } from "../Styles/GlobalStyle";
import { useDetectClickOutside } from "react-detect-click-outside";

const StyledNavbar = styled.nav`
    background: ${color.white};
    position: fixed;
    top: 0;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-between;
    align-items: center;
    padding: 2.1875rem 7%;
    z-index: 999;
    box-shadow: 0 0 ${(props) => (props.isNavbarOpen ? "1000px 1000px" : "0")}
        #00000050;
    transition: box-shadow 0.25s;

    & a + img {
        position: relative;
        bottom: 7px;
    }

    @media all and (min-width: 768px) {
        padding: 4rem 5%;
        box-shadow: 0 0 0 0;
    }

    @media all and (min-width: 1200px) {
    }

    .menu-button {
        grid-column: 2 / 3;
        justify-self: end;

        @media all and (min-width: 768px) {
            display: none;
        }
    }
            
    @media all and (min-width: 1800px) {
        padding-inline: 15%;
    }
`;

const NavLinks = styled.div`
    background: ${color.black};
    padding: 3rem 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    left: 0;
    right: 0;
    transition: all 0.5s;
    transform: translateY(-350px);
    opacity: 0;

    &.show {
        transform: translateY(160px);
        opacity: 1;
    }

    @media all and (min-width: 768px) {
        background: ${color.white};
        color: ${color.black};
        flex-direction: row;
        transform: unset;
        opacity: 1;
        position: static;
        padding: 0;

        &.show {
            transform: translateY(0);
        }
    }
    
`;

const Link = styled.a`
    text-decoration: none;
    text-transform: uppercase;
    color: ${color.white};
    font-size: 1.5rem;
    line-height: 25px;
    letter-spacing: 2px;
    margin-bottom: 2rem;
    position: relative;
    
    &:last-of-type {
        margin: 0;
    }

    @media all and (min-width: 768px) {
        color: ${color.darkGrey};
        font-size: 0.875rem;
        line-height: 0.875rem;
        margin: 0;
    }

    @media all and (min-width: 1200px) {
        &::after {
            content: "";
            background-color: #00000050;
            position: absolute;
            bottom: -5px;
            left: 0;
            width: 0;
            transition: all 0.5s;
        }

        &:hover::after {
            width: 100%;
            height: 1px;
        }
    }


`;

export default function Navbar(props) {
    const [isNavbarOpen, setIsNavbarOpen] = useState(false);

    const toggleNavbar = () => {
        setIsNavbarOpen((prev) => !prev);
    };

    const handleClick = (page) => {
        setIsNavbarOpen(false);
        props.changeCurrentPage(page);
    };

    const ref = useDetectClickOutside({
        onTriggered: () => setIsNavbarOpen(false),
    });

    return (
        <StyledNavbar isNavbarOpen={isNavbarOpen} ref={ref}>
            <a href="#home" onClick={() => handleClick("home")}>
                <Logo />
            </a>
            <img
                className="menu-button"
                src={isNavbarOpen ? closeButton : menu}
                alt="menu button"
                onClick={toggleNavbar}
            />
            <NavLinks className={isNavbarOpen ? "show" : "hide"}>
                <Link href="#about" onClick={() => handleClick("about")}>
                    our company
                </Link>
                <Link
                    href="#locations"
                    onClick={() => handleClick("locations")}
                >
                    locations
                </Link>
                <Link href="#contact" onClick={() => handleClick("contact")}>
                    contact
                </Link>
            </NavLinks>
        </StyledNavbar>
    );
}
